<template>
    <div class="box">
        <navbar></navbar>
        <div class="content">
            <div class="center-box warp-1200 ov">
                <centerbar></centerbar>
                <div class="right fl">
                    <p class="balance"><span>备份密码</span></p>
                    <div class="my-code">
                        <p class="titl">请截图或者使用文本，妥善备份您的账号密码，账户一旦丢失，账户内的资产永久不可找回。</p>
                        <div class="in-box">
                            <p>您的账号</p>
                            <div class="user ov"><span class="fl">{{backupData.username}}</span><i class="fl  copy" @click="copy(backupData.username)">复制</i></div>
                        </div>
                        <div class="in-box ibox">
                            <p>您的密码</p>
                            <div class="user ov"><span class="fl">{{backupData.password}}</span><i class="fl copy"  @click="copy(backupData.password)">复制</i></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Navbar, Centerbar } from '@/components'
    import {backup} from "../../api/center";
    import Clipboard from 'clipboard'
    export default {
        name: "Invite",
        components: {
            Navbar,
            Centerbar
        },
        data(){
            return {
                codeState:false,
                backupData:{
                   username:'',
                   password:''
                }
            }
        },
        mounted()
        {
            this.getBackup()
        },
        methods: {

            getBackup()
            {
                backup().then(res => {
                   this.backupData = res

                })
            },

            // 复制邀请码
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    this.$message({
                        message: '复制成功',
                        type: 'success'
                    });
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    this.$message({
                        message: '复制失败, 当前浏览器不支持',
                        type: 'error'
                    });
                    // 释放内存
                    clipboard.destroy()
                })

            },

        }
    }
</script>

<style scoped>
     @import "../../static/css/backup.css";
     .box {height: 100%;background: #F2F4F9;}
</style>



